import React, {Component} from 'react'
import axios from 'axios'
import toastr from 'toastr'
import {uploadPic} from '../../services/userService'
import {Menu, Icon} from 'antd';
import {Link} from 'react-router-dom';



class Profile extends Component{

    state = {
        user:{},
        clientes: []
    }

    componentWillMount(){
        const user = JSON.parse(localStorage.getItem('user'))
        if(!user) return this.props.history.push('/login')
        this.setState({user})
    }

    getPrivateInfo = () => {
        axios.get('http://localhost:3000/private', {
            headers:{
                "Authorization" : localStorage.getItem('token') 
            }
        })
        .then(res=>{
            console.log(res)
        })
        .catch(e=>toastr.error("algo falló", e.message))
    }

    

    uploadPhoto = () => {
        this.refs.input.click()
    }

    onChangeFile = (e) => {
        console.log(e.target.files[0])
        uploadPic(e.target.files[0])
        .then(pic=>console.log(pic))
        .catch(e=>toastr.error('Error'))
    }

    componentDidMount() {
        axios.get('http://localhost:3000/pictures')
            .then(res => {
                //console.log(res.data.formGet);
                const clientes = [...this.state.clientes];
                clientes.push(res.data.formGet);
                
                this.setState({
                    clientes: clientes[0]
                })
                console.log(this.state.clientes)

            }).catch(err => console.log(err))
    }

    render(){

       
        return(

        <div  className="formulario">
        
            <div style={{maxWidth: 200, minWidth: 250}} className="profileCard">

            
                    <Menu className="profileCard">

                       
                        
                            <Menu.Item key="/admin">
                                <Link to="/profile">
                                <Icon type="profile" /><span>Profile</span>
                                </Link>
                            </Menu.Item>

         
                            
                            <Menu.Item key="/admin/projects">
                                <Link to="/admin/projects" >
                                <span>Tus nuevos pedidos.</span>
                                </Link>
                            </Menu.Item>

                            <Menu.Item onClick={this.logOut} key="/admin/logout">
                                <Link to="/">
                                <Icon type="poweroff" /> <span>Logout</span>
                                </Link>
                            </Menu.Item>

                        
                    </Menu>    
                        
        
           </div>
            
            {this.state.clientes.map((cliente, indice) => (
                <div key={indice} className="clientes">

                    <p><strong>Nombre del cliente:</strong> {cliente.nombre}</p>
                    <p><strong>Apellido del cliente:</strong> {cliente.apellido}</p>
                    <p><strong>Pais:</strong>{cliente.pais}</p>
                    <p><strong>Ciudad:</strong>{cliente.ciudad}</p>
                    <p><strong>C.P:</strong>{cliente.postal}</p>
                    <p><strong>Forma de pago:</strong>{cliente.pago}</p>
                    <p><strong>Correo:</strong>{cliente.correo}</p>
                    <p><strong>Telefono:</strong>{cliente.telefono}</p>
                    
                    <p><strong>Tamaño botella:</strong>{cliente.botella}</p>
                    <p><strong>Cantidad de botella:</strong>{cliente.numbotella}</p>
                    <p><strong>Tipo de caja:</strong>{cliente.caja}</p>
                    <p><strong>Cantidad de cajas:</strong>{cliente.numcaja}</p>

                </div>
            ))}
           

        </div>
        )
    }
}

export default Profile